import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

// import Custom Components
import PageHeader from '../../common/page-header';
import Breadcrumb from '../../common/breadcrumb';
import ProfileTwo from '../../features/profile/profile-two';
import IconBox from '../../features/icon-box';

import { countTo } from '../../../utils';
import data from '../../../mock_data/data.json';

function OrderCompleted() {
    useEffect( () => {
        countTo();
    } )

    return (
        <div className="main">
            <Helmet>
                <title>Đơn hàng đã được đặt thành công!</title>
            </Helmet>

            <h1 className="d-none">Đơn hàng đã được đặt thành công!</h1>

            <PageHeader title="Đặt hàng thành công!" subTitle="Chúng tôi sẽ sớm liên hệ bạn để xác nhận đơn hàng." />
            <Breadcrumb title="Đặt hàng thành công!"  />

            <div className="page-content pb-3">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-10 offset-lg-1">
                            <div className="about-text text-center mt-3">
                                <h2 className="title text-center mb-2">Bạn đã đặt hàng thành công!</h2>
                                <p className="text-left ">Kính gửi quý khách hàng thân mến.</p>

                                <p className="text-left ">Chúng tôi xin chân thành cảm ơn bạn đã ghé thăm trang web và mua hàng của chúng tôi. Chúng tôi thực sự rất vui vì bạn đã tìm thấy những gì mà bạn đang cần tìm kiếm ở tại trang web của chúng tôi.</p>

                                <p className="text-left mb-2">Phương châm của chúng tôi là luôn đặt sự hài lòng của khách hàng lên hàng đầu. Vì vậy, nếu bạn không thật sự hài lòng với dịch vụ, cũng như trải nghiệm tại trang web của chúng tôi. Chúng tôi rất muốn biết đó là gì để có thể kịp thời khắc phục. Những đóng góp quý giá của bạn chính là động lực để chúng tôi ngày càng hoàn thiện hơn nữa. Một lần nữa, chúng tôi mong rằng sẽ được phục vụ bạn nhiều lần hơn nữa.</p>

                                <p className="text-left mb-2">Chúc bạn một ngày tốt lành! </p>
                                <img src={ `${process.env.PUBLIC_URL}/assets/images/about/about-2/signature.png` } alt="signature" className="mx-auto mb-5" />
                                <div className="text-center mt-3">
                                    <Link to={ `${process.env.PUBLIC_URL}` } className="btn btn-sm btn-minwidth-lg btn-outline-primary-2">
                                        <span>TIẾP TỤC MUA SẮM</span>
                                        <i className="icon-long-arrow-right"></i>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>


                
            </div>
        </div>
    )
}

export default React.memo( OrderCompleted );