import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

// import Custom Components
import Breadcrumb from '../../common/breadcrumb';
import ProfileOne from '../../features/profile/profile-one';
import OwlCarousels from '../../features/owl-carousel';
import Testimonial from '../../features/testimonial';

// import Utils
import { mainSlider5 } from '../../settings';

import _data from '../../../mock_data/data.json';

function AboutOne () {
    useEffect( () => {
        document.querySelector( ".footer-middle" ) && document.querySelector( ".footer-middle" ).classList.add( "border-0" );
    } )

    return (
        <div className="main">
            <Helmet>
                <title>Vanis Store - About Page</title>
            </Helmet>

            <h1 className="d-none">Vanis Store - About Page</h1>

            <Breadcrumb title="Giới thiệu"  adClass="border-0 mb-0" />

            <div className="container">
                <div className="page-header page-header-big text-center" style={ { backgroundImage: `url(${process.env.PUBLIC_URL}/assets/images/about-header-bg.jpg)` } }>
                    <h1 className="page-title text-white">Vanis Store<span className="text-white">Thời Trang Và Hơn Thế Nữa</span></h1>
                </div>
            </div>

            <div className="page-content pb-0">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 mb-3 mb-lg-0">
                            <h2 className="title">Tầm nhìn</h2>
                            <p>Shop Thời Trang Online Vanis Store với phương châm "Đồng hành cùng phong cách thời trang của bạn" sẽ là nơi mua sắm an toàn và uy tín, bởi chúng tôi luôn đề cao tiêu chí mang đến cho quý khách những sản phẩm chất lượng nhất với giá cả luôn phải chăng. 
                            </p>
                            <p>Shop Thời Trang Love Tree chuyên cung cấp:</p>
                            <p>- Thời trang công sở</p>
                            <p>- Thời trang nam, nơi</p>
                            <p>- Thời trang cặp và đồng phục</p>
                            <p>với nhiều mặt hàng đa dạng và phong phú như áo thun, áo sơ mi, áo vest, đầm, váy, quần jean, quần tây,.... sẽ là sự lựa chọn tốt nhất cho phong cách thời của các bạn.
  
 Đặc biệt, hiện nay, shop đang hợp tác cùng với các xưởng thời trang công sở đặt may quần áo công sở theo số đo của quý khách và in áo đồng phục, áo nhóm số lượng lớn.
  
 Tuy Shop Thời Trang Love Tree hiện chủ yếu chỉ bán hàng online nhưng luôn luôn đảm bảo uy tín và chất lượng sản phẩm (Nếu trong quá trình mua - bán hàng xảy ra lỗi từ phía shop, chúng tôi sẽ hoàn trả tiền và bồi thường thiệt hại cho quý khách) </p>
                        </div>

                        <div className="col-lg-6">
                            <h2 className="title">Giới thiệu</h2>
                            <p>Ra đời từ năm 2012, chỉ từ một cửa hàng thời trang
                                nhỏ, đến nay Vanis Store đã không ngừng phát triển
                                và trở thành một hệ thống cửa hàng chuyên kinh
                                doanh thời trang trẻ, là một trong những thương
                                hiệu thời trang hàng đầu tại Đà Nẵng.... </p>
                        </div>
                    </div>

                    <div className="mb-5"></div>
                </div>

                

               
            </div>
        </div>
    )
}

export default React.memo( AboutOne );